.slider {
  position: relative;
  max-width: 1160px;

  &__button {
    padding: 0;
    width: 16px;
    height: 32px;

    display: inline-block;

    background-color: transparent;

    position: absolute;

    &::before {
      height: 16px;
      width: 1px;

      position: absolute;
      top: 2px;
      left: 6px;

      rotate: 45deg;

      content: "";

      background-color: $color-dark-gray;
    }

    &::after {
      height: 16px;
      width: 1px;

      position: absolute;
      bottom: 2px;
      left: 6px;

      rotate: -45deg;

      content: "";

      background-color: $color-dark-gray;
    }

    &--prev {
      left: -5%;
      top: 50%;

      @media (max-width: 1360px) {
        left: -3%;
      }

      @include vp-1023 {
        left: -11%;
      }
    }

    &--next {
      rotate: 180deg;
      right: -5%;
      top: 50%;

      @media (max-width: 1360px) {
        right: -3%;
      }

      @include vp-1023 {
        right: -11%;
      }
    }

    &:hover {
      scale: 1.3;
    }

    &:focus {
      outline: none;
      scale: 1.3;
    }

    &:active {
      scale: 0.8;
    }
  }
}
